@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
.app-footer {
    border-top: 1px rgba(197, 188, 188, 0.8) solid;
    margin: 0 40px;
}

.app-footer p {
    font-size: 18px;
    color: rgba(197, 188, 188, 0.8);
}
.gallery-item {
    padding: 10px;
    list-style-type: none;
    cursor: pointer;
}
.gallery {
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
    max-width: 1000px;
    justify-content: center;
    margin: 0 auto;
}
.lds-ring {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
    align-self: center;
    margin: 0 auto;
  }
  .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid #fff;
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: rgb(180, 58, 58) transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
    animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
    animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
    animation-delay: -0.15s;
  }
  @keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
