.modal {
    position: fixed; 
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,0.5); 
    z-index: 1050;
    opacity: 1;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
}

.modal-dialog {
    position: relative;
    margin: 0 auto;
}

.modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: .3rem;
    outline: 0;
}

.modal-header {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 15px;
}

.modal-title {
    margin-top: 0;
    margin-bottom: 0;
    line-height: 1.5;
    font-size: 1.25rem;
    font-weight: 500;
}
.close {
    font-family: sans-serif;
    font-size: 34px;
    font-weight: 300;
    line-height: 1;
    color: #000;
    opacity: .5;
    text-decoration: none;
    background: none;
    border: none;
    outline: none;
}

.close:focus, .close:hover {
    color: #000;
    text-decoration: none;
    cursor: pointer;
    opacity: .75;
}

.modal-body {
    position: relative;
    flex: 1 1 auto;
    padding: 15px;
    overflow: auto;
    display: flex;
}

.modal-body img {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
}

.modal-comments {
    padding: 0 40px;
}

.modal-comment {
    text-align: start;
    font-size: 14px;
}

.modal-comment span{
    color: gray;
}

.modal-form {
    display: flex;
    flex-direction: column;
}

.modal-form input {
    padding: 5px;
    margin-bottom: 15px;
}

.modal-form button {
    outline: none;
    border: none;
    background-color: #4997D0;
    border-radius: 3px;
    color: #fff;
    font-size: 14px;
    padding: 10px 0;
}
.app {
    height: 100vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
body {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}
